@import url(https://fonts.googleapis.com/css?family=Raleway:400, 700, 800);
*, *:after, *:before {
	 box-sizing: border-box;
}
 html {
	 height: 100%;
}
 body {
	 height: 100%;
	 color: #fffce1;
	 font-family: 'Raleway', Arial, sans-serif;
     margin: 0;
}
 #app {
	 height: 100%;
}
 a {
	 color: #4e4a46;
	 text-decoration: none;
     font-weight: bold;
}
 a:hover, a:focus {
	 color: #c94e50;
}
 main {
	 height: 100%;
	 padding: 3em 2em;
	 text-align: center;
	 background: #a0a4b5;
	 overflow: auto;
}
 @media screen and (max-width: 40em) {
	 main {
		 font-size: 80%;
	}
}
